.header {
	grid-area: h;
	background-color: #1f27af;
	display: flex;
}

.header img {
	width: 150px;
	height: 100px;
}

.header .lightgreenBlock {
	width: 10px;
	background-color: #b1e7c0;
}

.header h1 {
	flex: 1;
	color: #b1e5e7;
	text-align: center;
	font-size: 75px;
	line-height: 100px;
	margin: 0;
}
