.main {
	display: flex;
	flex-direction: column;
	border: 2px solid #b1e5e7;
	border-radius: 20px;
	padding: 20px;
	min-width: 220px;
	max-width: 220px;
	h1 {
		width: 100%;
		margin: 0 auto 10px;
		text-align: center;
	}
	p {
		margin: 0;
		font-size: 14px;
		b {
			color: white;
		}
	}
}
.formBlock {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 10px;
	margin-top: 10px;
}
.button {
	align-self: center;
	button {
		padding: 5px 10px;
	}
}
.errorServer {
	color: red;
	background-color: white;
	font-weight: bolder;
	padding: 2px 5px;
	border-radius: 5px;
	text-align: center;
}
